import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'
import { v4 as uuidv4 } from 'uuid'
import type { 
  AppointmentType, 
  MeetingLocationType, 
  VideoProvider,
  CustomQuestion 
} from '@/lib/calendar/types'

// ==========================================
// GET /api/v1/calendars/[calendarId]/appointment-types
// Get all appointment types for a calendar
// ==========================================
export async function GET(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const typeId = searchParams.get('id')
    const activeOnly = searchParams.get('activeOnly') !== 'false'

    // Verify calendar exists
    const { data: calendar, error: calError } = await supabase
      .from('calendars')
      .select('id, name, sub_account_id')
      .eq('id', calendarId)
      .single()

    if (calError || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found' },
        { status: 404 }
      )
    }

    // Get specific type if ID provided
    if (typeId) {
      const { data: appointmentType, error } = await supabase
        .from('appointment_types')
        .select('*')
        .eq('id', typeId)
        .eq('calendar_id', calendarId)
        .single()

      if (error || !appointmentType) {
        return NextResponse.json(
          { success: false, error: 'Appointment type not found' },
          { status: 404 }
        )
      }

      return NextResponse.json({
        success: true,
        data: transformAppointmentType(appointmentType)
      })
    }

    // Get all appointment types
    let query = supabase
      .from('appointment_types')
      .select('*')
      .eq('calendar_id', calendarId)
      .order('display_order', { ascending: true })

    if (activeOnly) {
      query = query.eq('is_active', true)
    }

    const { data: appointmentTypes, error } = await query

    if (error) {
      console.error('Error fetching appointment types:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to fetch appointment types' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: (appointmentTypes || []).map(transformAppointmentType)
    })

  } catch (error) {
    console.error('Error in GET /api/v1/calendars/[calendarId]/appointment-types:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// POST /api/v1/calendars/[calendarId]/appointment-types
// Create a new appointment type
// ==========================================
export async function POST(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      name,
      description,
      durationMinutes = 30,
      bufferBeforeMinutes = 0,
      bufferAfterMinutes = 0,
      isActive = true,
      minimumNoticeHours = 0,
      maximumBookingDays = 60,
      defaultLocationType,
      defaultLocation,
      videoProvider,
      price,
      currency = 'USD',
      requirePayment = false,
      depositAmount,
      customQuestions = [],
      maxCapacity,
      color,
      icon,
      displayOrder
    } = body

    // Validation
    if (!name) {
      return NextResponse.json(
        { success: false, error: 'name is required' },
        { status: 400 }
      )
    }

    // Verify calendar exists
    const { data: calendar, error: calError } = await supabase
      .from('calendars')
      .select('id')
      .eq('id', calendarId)
      .single()

    if (calError || !calendar) {
      return NextResponse.json(
        { success: false, error: 'Calendar not found' },
        { status: 404 }
      )
    }

    // Generate unique slug
    const baseSlug = name
      .toLowerCase()
      .replace(/[^a-z0-9]+/g, '-')
      .replace(/^-|-$/g, '')
    const uniqueSlug = `${baseSlug}-${uuidv4().slice(0, 8)}`

    // Get highest display order if not provided
    let finalDisplayOrder = displayOrder
    if (finalDisplayOrder === undefined) {
      const { data: lastType } = await supabase
        .from('appointment_types')
        .select('display_order')
        .eq('calendar_id', calendarId)
        .order('display_order', { ascending: false })
        .limit(1)
        .single()

      finalDisplayOrder = (lastType?.display_order || 0) + 1
    }

    // Validate location type
    if (defaultLocationType) {
      const validLocationTypes: MeetingLocationType[] = ['physical', 'phone', 'video', 'custom', 'ask_invitee']
      if (!validLocationTypes.includes(defaultLocationType)) {
        return NextResponse.json(
          { success: false, error: `Invalid defaultLocationType. Must be one of: ${validLocationTypes.join(', ')}` },
          { status: 400 }
        )
      }
    }

    // Validate video provider
    if (videoProvider) {
      const validProviders: VideoProvider[] = ['zoom', 'google_meet', 'teams', 'custom']
      if (!validProviders.includes(videoProvider)) {
        return NextResponse.json(
          { success: false, error: `Invalid videoProvider. Must be one of: ${validProviders.join(', ')}` },
          { status: 400 }
        )
      }
    }

    // Create appointment type
    const typeId = uuidv4()
    const { data: appointmentType, error } = await supabase
      .from('appointment_types')
      .insert({
        id: typeId,
        calendar_id: calendarId,
        name,
        description,
        slug: uniqueSlug,
        duration_minutes: durationMinutes,
        buffer_before_minutes: bufferBeforeMinutes,
        buffer_after_minutes: bufferAfterMinutes,
        is_active: isActive,
        minimum_notice_hours: minimumNoticeHours,
        maximum_booking_days: maximumBookingDays,
        default_location_type: defaultLocationType,
        default_location: defaultLocation,
        video_provider: videoProvider,
        price,
        currency,
        require_payment: requirePayment,
        deposit_amount: depositAmount,
        custom_questions: customQuestions,
        max_capacity: maxCapacity,
        color,
        icon,
        display_order: finalDisplayOrder
      })
      .select()
      .single()

    if (error) {
      console.error('Error creating appointment type:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to create appointment type' },
        { status: 500 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformAppointmentType(appointmentType)
    }, { status: 201 })

  } catch (error) {
    console.error('Error in POST /api/v1/calendars/[calendarId]/appointment-types:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PUT /api/v1/calendars/[calendarId]/appointment-types
// Update an appointment type
// ==========================================
export async function PUT(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const {
      id,
      name,
      description,
      durationMinutes,
      bufferBeforeMinutes,
      bufferAfterMinutes,
      isActive,
      minimumNoticeHours,
      maximumBookingDays,
      defaultLocationType,
      defaultLocation,
      videoProvider,
      price,
      currency,
      requirePayment,
      depositAmount,
      customQuestions,
      maxCapacity,
      color,
      icon,
      displayOrder
    } = body

    if (!id) {
      return NextResponse.json(
        { success: false, error: 'id is required' },
        { status: 400 }
      )
    }

    // Build update object
    const updates: Record<string, any> = {
      updated_at: new Date().toISOString()
    }

    if (name !== undefined) updates.name = name
    if (description !== undefined) updates.description = description
    if (durationMinutes !== undefined) updates.duration_minutes = durationMinutes
    if (bufferBeforeMinutes !== undefined) updates.buffer_before_minutes = bufferBeforeMinutes
    if (bufferAfterMinutes !== undefined) updates.buffer_after_minutes = bufferAfterMinutes
    if (isActive !== undefined) updates.is_active = isActive
    if (minimumNoticeHours !== undefined) updates.minimum_notice_hours = minimumNoticeHours
    if (maximumBookingDays !== undefined) updates.maximum_booking_days = maximumBookingDays
    if (defaultLocationType !== undefined) updates.default_location_type = defaultLocationType
    if (defaultLocation !== undefined) updates.default_location = defaultLocation
    if (videoProvider !== undefined) updates.video_provider = videoProvider
    if (price !== undefined) updates.price = price
    if (currency !== undefined) updates.currency = currency
    if (requirePayment !== undefined) updates.require_payment = requirePayment
    if (depositAmount !== undefined) updates.deposit_amount = depositAmount
    if (customQuestions !== undefined) updates.custom_questions = customQuestions
    if (maxCapacity !== undefined) updates.max_capacity = maxCapacity
    if (color !== undefined) updates.color = color
    if (icon !== undefined) updates.icon = icon
    if (displayOrder !== undefined) updates.display_order = displayOrder

    const { data: appointmentType, error } = await supabase
      .from('appointment_types')
      .update(updates)
      .eq('id', id)
      .eq('calendar_id', calendarId)
      .select()
      .single()

    if (error || !appointmentType) {
      console.error('Error updating appointment type:', error)
      return NextResponse.json(
        { success: false, error: 'Failed to update appointment type or type not found' },
        { status: error ? 500 : 404 }
      )
    }

    return NextResponse.json({
      success: true,
      data: transformAppointmentType(appointmentType)
    })

  } catch (error) {
    console.error('Error in PUT /api/v1/calendars/[calendarId]/appointment-types:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// DELETE /api/v1/calendars/[calendarId]/appointment-types
// Delete an appointment type
// ==========================================
export async function DELETE(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const { searchParams } = new URL(request.url)
    
    const typeId = searchParams.get('id')
    const permanent = searchParams.get('permanent') === 'true'

    if (!typeId) {
      return NextResponse.json(
        { success: false, error: 'id is required' },
        { status: 400 }
      )
    }

    // Check if there are existing appointments using this type
    const { count } = await supabase
      .from('appointments')
      .select('id', { count: 'exact', head: true })
      .eq('appointment_type_id', typeId)
      .in('status', ['scheduled', 'confirmed'])

    if (count && count > 0 && permanent) {
      return NextResponse.json(
        { success: false, error: `Cannot delete appointment type with ${count} active appointments. Deactivate instead.` },
        { status: 400 }
      )
    }

    if (permanent) {
      // Permanent delete
      const { error } = await supabase
        .from('appointment_types')
        .delete()
        .eq('id', typeId)
        .eq('calendar_id', calendarId)

      if (error) {
        console.error('Error deleting appointment type:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to delete appointment type' },
          { status: 500 }
        )
      }
    } else {
      // Soft delete (deactivate)
      const { error } = await supabase
        .from('appointment_types')
        .update({ 
          is_active: false,
          updated_at: new Date().toISOString()
        })
        .eq('id', typeId)
        .eq('calendar_id', calendarId)

      if (error) {
        console.error('Error deactivating appointment type:', error)
        return NextResponse.json(
          { success: false, error: 'Failed to deactivate appointment type' },
          { status: 500 }
        )
      }
    }

    return NextResponse.json({
      success: true,
      message: permanent ? 'Appointment type deleted' : 'Appointment type deactivated'
    })

  } catch (error) {
    console.error('Error in DELETE /api/v1/calendars/[calendarId]/appointment-types:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// PATCH /api/v1/calendars/[calendarId]/appointment-types
// Bulk update display order (for drag-and-drop reordering)
// ==========================================
export async function PATCH(
  request: NextRequest,
  { params }: { params: Promise<{ calendarId: string }> }
) {
  try {
    const { calendarId } = await params
    const supabase = supabaseAdmin
    const body = await request.json()

    const { typeOrder } = body // Array of { typeId, displayOrder }

    if (!typeOrder || !Array.isArray(typeOrder)) {
      return NextResponse.json(
        { success: false, error: 'typeOrder array is required' },
        { status: 400 }
      )
    }

    // Update each type's display order
    const updatePromises = typeOrder.map(({ typeId, displayOrder }: { typeId: string; displayOrder: number }) =>
      supabase
        .from('appointment_types')
        .update({ display_order: displayOrder })
        .eq('id', typeId)
        .eq('calendar_id', calendarId)
    )

    await Promise.all(updatePromises)

    return NextResponse.json({
      success: true,
      message: 'Display order updated successfully'
    })

  } catch (error) {
    console.error('Error in PATCH /api/v1/calendars/[calendarId]/appointment-types:', error)
    return NextResponse.json(
      { success: false, error: 'Internal server error' },
      { status: 500 }
    )
  }
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function transformAppointmentType(dbType: any): AppointmentType {
  return {
    id: dbType.id,
    calendarId: dbType.calendar_id,
    name: dbType.name,
    description: dbType.description,
    slug: dbType.slug,
    durationMinutes: dbType.duration_minutes,
    bufferBeforeMinutes: dbType.buffer_before_minutes,
    bufferAfterMinutes: dbType.buffer_after_minutes,
    isActive: dbType.is_active,
    minimumNoticeHours: dbType.minimum_notice_hours,
    maximumBookingDays: dbType.maximum_booking_days,
    defaultLocationType: dbType.default_location_type,
    defaultLocation: dbType.default_location,
    videoProvider: dbType.video_provider,
    price: dbType.price,
    currency: dbType.currency,
    requirePayment: dbType.require_payment,
    depositAmount: dbType.deposit_amount,
    customQuestions: dbType.custom_questions || [],
    maxCapacity: dbType.max_capacity,
    color: dbType.color,
    icon: dbType.icon,
    displayOrder: dbType.display_order,
    createdAt: dbType.created_at,
    updatedAt: dbType.updated_at
  }
}
